.login__content {
  width: 340px;
  height: 650px;
  background: #fff;
  border-radius: 12px;
  flex-direction: column;
  justify-content: space-evenly;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 0 6px #00000024;
}

.login__btn-conteiner {
  flex-direction: column;
  justify-content: space-between;
  display: flex;
}

.login__btn-conteiner a {
  margin-top: 20px;
}

.auth__button {
  width: 280px;
  height: 37px;
  color: #fff;
  background: #3369f3;
  border-radius: 8px;
}

.auth__button:hover {
  cursor: pointer;
}

